@import "./global.scss";

.App {
  background: linear-gradient(to right, rgb(23, 36, 95), transparent),
    url(https://grainy-gradients.vercel.app/noise.svg);
  overflow-x: hidden;
}
.sections {
  overflow-x: hidden;

  width: calc(70px + 100vw);
  height: 100vh;

  scroll-behavior: smooth;
  scroll-snap-type: y mandatory;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    width: 10px;
    //background-color: rgb(0, 11, 71);
  }
  &::-webkit-scrollbar-thumb {
    direction: rtl;
    border-radius: 5px;
    background: rgb(230, 242, 255);
  }

  > * {
    border-radius: 5px;
    width: 100vw;
    height: 100vh;
    scroll-snap-align: start;
  }
  .cont {
    padding-top: 0px;
  }
}
