.about {
  height: 100%;
  position: relative;
  .container {
    overflow-x: hidden;
    width: 80%;
    height: 80%;
    color: rgb(255, 255, 255);
    background-color: rgb(32, 47, 48, 0.2);
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 20px;
    padding: 20px;
    padding-top: 10px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: calc(1vw + 10px);
    position: absolute;
    @media only screen and (max-width: 768px) {
      font-size: calc(1vw + 12px);
    }

    &::-webkit-scrollbar {
      width: 10px;
      //background-color: rgb(0, 11, 71);
    }
    &::-webkit-scrollbar-thumb {
      direction: rtl;
      border-radius: 5px;
      background: rgb(230, 242, 255);
    }

    > * {
      border-radius: 5px;
      padding-top: 20px;

      scroll-snap-align: start;
    }
  }
  @media only screen and (max-width: 700px) {
    .container {
      min-width: 95%;
      padding: 10px;
      padding-top: 10px;
      height: 85%;
      margin-top: 20px;
    }
  }
}
