@import "../../global.scss";

.topbar {
  overflow: hidden;
  width: 100%;
  height: 65px;
  background-color: $navbar;
  color: white;
  position: fixed;
  top: 0;
  z-index: 3;
  backdrop-filter: blur(1px);
  box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.329);
  transition: background-color 0.6s ease;
  display: flex;
  flex-direction: row;

  .wrapper {
    width: 100%;
    padding: 10px 30px;
    display: flex;
    align-items: center;

    .left {
      width: 27%;
      align-items: center;
      padding-right: 50px;
      .logo {
        color: white;
        font-style: italic;
        font-size: 33px;
        font-weight: 700;
        text-decoration: none;
        color: inherit;
        margin-right: 10px;
        &:hover {
          color: rgb(10, 0, 66);
          transition: all 0.4s ease;
        }
      }
    }
    .middle {
      width: 60%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .itemContainer {
        display: flex;
        align-items: center;

        margin-left: 10px;
        .icon {
          font-size: 40px;
          margin-right: 0px;
          transition: opacity 0.5s ease;
        }
        a {
          text-decoration: none;
          color: white;
          font-size: 36px;
          font-weight: 400;
          &:hover {
            color: rgb(10, 0, 66);
            transition: all 0.4s ease;
          }
        }
      }
    }
    @media only screen and (max-width: 700px) {
      .middle {
        display: none;
      }
    }
    .right {
      position: absolute;
      right: 25px;
      .hamburger {
        width: 32px;
        height: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;

        span {
          width: 100%;
          height: 3px;
          background-color: rgb(23, 36, 95);
          transform-origin: left;
          transition: all 0.6s ease;
        }
      }
    }
  }
  &.active {
    .icon {
      opacity: 0;
    }
    .hamburger {
      span {
        &:first-child {
          background-color: white;
          transform: rotate(45deg);
        }
        &:nth-child(2) {
          opacity: 0;
        }
        &:last-child {
          background-color: white;
          transform: rotate(-45deg);
        }
      }
    }
  }
}
