.Main {
  height: 100%;
  position: relative;

  .container {
    width: 80%;
    height: 80%;
    color: rgb(255, 255, 255);
    background-color: rgb(32, 47, 48, 0.2);
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 20px;
    padding: 40px;
    padding-top: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-flow: row wrap;

    &::-webkit-scrollbar {
      width: 10px;
      //background-color: rgb(0, 11, 71);
    }
    &::-webkit-scrollbar-thumb {
      direction: rtl;
      border-radius: 5px;
      background: rgb(230, 242, 255);
    }

    > * {
      border-radius: 5px;
      padding-top: 70px;

      scroll-snap-align: start;
    }
    overflow-x: hidden;
    .container1 {
      flex: 1;
      padding-top: 20px;
      min-width: 450px;
      width: 450px;
      font-size: 20px;
      margin: 0px 20px 20px 0;
      padding-right: 5vw;
      padding-left: 2vw;
    }
    .container2 {
      flex: 0.6;
      //margin-right: 20px;
      min-width: 450px;
      margin-left: auto;
      margin-right: auto;
    }
    img {
      border-radius: 30px;
      width: 450px;
    }
  }
  @media only screen and (max-width: 768px) {
    .container {
      min-width: 95%;
      padding: 0px;
      padding-top: 10px;
      .container2 {
        min-width: 300px;
      }
      img {
        border-radius: 30px;
        width: 300px;
      }
    }
  }
}
